import Penalty from './Penalty'
const lsa = require('../data/lsa.json')

export default class Article {
  lawTitle: string
  lawTitleAbbr: string
  id: string
  paragraph: number
  body: string[]
  entity: Object
  url: string

  constructor (lawTitle?: string, id?: string, paragraph?: number) {
    this.lawTitle = lawTitle
    this.id = id
    this.paragraph = paragraph

    if (lawTitle === '') {
      this.lawTitleAbbr = lsa['LAWS'][''][''] || ''
      const articles = lsa['LAWS']['']['']['']
      this.entity = articles.find(article => article[''] === id)
    }
  }

  setUrl (url: string): Article {
    this.url = url
    return this
  }

  setBody (body: string[]): Article {
    this.body = body
    return this
  }

  getBody (): string[] {
    if (this.entity) {
      return this.entity[''].zh
    } else {
      return this.body
    }
  }

  penalize (): Penalty {
    let article: Article
    if (this.entity[''].length === 1) {
      const { id, paragraph } = this.entity[''][0].penalty
      article = new Article(this.lawTitle, id, paragraph)
    } else if (this.paragraph !== undefined) {
      const ref = this.entity['']
                      .find(ref => ref.paragraph === this.paragraph)

      if (!ref) {
        throw new Error(` ${this.id}  ${this.paragraph} `)
      }

      article = new Article(this.lawTitle, ref.penalty.id, ref.paragraph)
    }

    return new Penalty(article)
  }
}



